      type: Number,
      default: 50, // ÈªòËÆ§50pxÔºåËΩ¨Êç¢‰∏∫remÊòØ3.125rem
    },
  },
  computed: {
    computedModalStyle() {
      return {
        ...this.modalStyle,
        paddingBottom: `${this.footerHeight / 16}rem`
      };
    },
    spacerStyle() {
      return {
        height: `${this.footerHeight / 16}rem`
      };
    }
  }
};
</script>

